*{
  margin: 0;
  padding: 0;
}
body{
  background-color: white;
}
.app{
  max-width: 400px;
  margin: auto;
  padding: 1rem;
  text-align: center;
}

.app .date {

  margin: 2rem 0 1rem 0;
  display: flex;

}

.app .date p {
  padding: 0 2px;
}

.app .form-input{
  position: relative;
  width: 100%;
  display: flex;
  align-self: center;
  justify-content: center;

}

.app .form-input .icon{
  position: absolute;
  left: 15px;
  top: 0;
  font-size: 25px;
  color: #4b00ff;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app form input{
  border: 2px solid grey;
  padding: 5px 10px 5px 50px;
  font-size: 18px;
  line-height: 30px;
  outline: 0;
  display: block;
  width: 100%;
  border-radius: 8em;
}
.app .task-row{
  border: 2px solid grey;
  padding: 10px ;
  font-size: 18px;
  margin: 8px 0;
  outline: 0;
  display: block;
  width: 94%;
  border-radius: 8em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.app .task-row .icon{
  color: #4b00ff;
  font-size: 25px;
}

.app .task-row p {
  text-transform: capitalize;
}

.completed {

  background-color: rgb(218, 216, 216);
  text-decoration: line-through;
}

.app .length
{
  margin: 1rem 0;
}